import { TextField } from "@mui/material";
import { Form, Formik, FormikHelpers } from "formik";
import React, { ChangeEvent, FC } from "react";
import { Button } from "../button";

import style from "./index.module.scss";
import { UploadImage } from "./uploadImage";
import { schema } from "./validateSchema";
import InputMask from "react-input-mask";
import { Position } from "../../../types/type";
import { PositionsRadioGroup } from "./positions-radio-group";
import apiClient from "../../../common/apiClient";
import { useUsersContext } from "../../../hooks/useUsersContext";
import useToken from "../../../hooks/useToken";
import useSubimitForm from "./useSubimitForm";

type FormProps = {
  positions: Position[];
};

export type ValuesFormik = {
  name: string;
  email: string;
  phone: string;
  position_id: string;
  photo: string;
};

const initialValues: ValuesFormik = {
  name: "",
  email: "",
  phone: "",
  position_id: "1",
  photo: "",
};

export const PostForm: FC<FormProps> = ({ positions }) => {
  const onSubmit = useSubimitForm();

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={onSubmit}
      validationSchema={schema}
    >
      {(formikProps) => {
        const {
          values,
          errors,
          touched,
          handleChange,
          handleSubmit,
          isSubmitting,
          setFieldValue,
          dirty,
        } = formikProps;

        const onChangePhoneNumber = (event: ChangeEvent<HTMLInputElement>) => {
          const value = event.target.value;
          setFieldValue("phone", value);
        };

        const onUploadFile = (event: React.ChangeEvent<HTMLInputElement>) => {
          const file = event.target.files?.[0];

          if(file) {
            setFieldValue("photo", file);
          }
        };

        console.log(errors);

        return (
          <Form className={style.wrapper} onSubmit={handleSubmit}>
            <TextField
              id="outlined-name"
              name="name"
              label="Your name"
              variant="outlined"
              color="secondary"
              className={style.form}
              value={values.name}
              onChange={handleChange}
              error={errors.name && touched.name ? true : false}
              helperText={errors.name && touched.name && errors.name}
            />
            <TextField
              id="outlined-email"
              label="Email"
              name="email"
              type="email"
              variant="outlined"
              color="secondary"
              className={style.form}
              value={values.email}
              onChange={handleChange}
              error={errors.email && touched.email ? true : false}
              helperText={errors.email && touched.email && errors.email}
            />
            {/* <InputMask
              maskChar={null}
              mask="+38 (099) 999 99 99"
              onChange={onChangePhoneNumber}
            >
              {() => ( */}
            <TextField
              id="outlined-phone"
              type="tel"
              name="phone"
              label="Phone"
              placeholder="Phone"
              variant="outlined"
              color="secondary"
              value={values.phone}
              error={errors.phone && touched.phone ? true : false}
              helperText={errors.phone && touched.phone && errors.phone}
              onChange={handleChange}
            />
            {/* )}
            </InputMask> */}

            <PositionsRadioGroup positions={positions} />
            {/* <TextField
              type="file"
              name="photo"
              placeholder="Image"
              variant="outlined"
              color="secondary"
              value={values.photo}
              onChange={handleChange}
            ></TextField> */}
            <UploadImage
              image={values.photo}
              onUploadImage={onUploadFile}
              error={errors.photo && touched.photo ? true : false}
            />
            <div className={style.buttonWrapper}>
              <Button color={dirty ? "primary" : "disabled"} type="submit">
                Sign up
              </Button>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
};
