import React, { FC, useMemo } from "react";
import { Button } from "../button";
import { Card } from "../card";

import cn from "classnames";

import style from "./index.module.scss";
import { useUsersContext } from "../../../hooks/useUsersContext";

export const GetModule: FC = () => {
  const { users, next, totalPages, pageState } = useUsersContext();

  const sortedUsers = useMemo(() => {
    return users.sort(
      (a, b) => b.registration_timestamp - a.registration_timestamp
    );
  }, [users]);

  const onShowMore = () => {
    if (next) {
      next();
    }
  };

  return (
    <div className={style.wrapper}>
      <div className={style.content}>
        <h1 className={style.title}>Working with GET request</h1>

        {
          <div className={style.cardsWrapper}>
            {sortedUsers?.map((user) => (
              <Card
                key={user.id}
                name={user.name}
                photo={user.photo}
                phone={user.phone}
                position={user.position}
                email={user.email}
              />
            ))}
          </div>
        }

        <div
          className={cn(style.buttonWrapper, {
            [style.invisible]: totalPages === pageState?.[0],
          })}
        >
          <Button
            color={!users.length ? "disabled" : "primary"}
            onClick={onShowMore}
          >
            Show more
          </Button>
        </div>
      </div>
    </div>
  );
};
